@charset "UTF-8";
/* You can add global styles to this file, and also import other style files */
/*==============================
Default CSS
==============================*/
:root {
  --fontFamily: "Poppins", sans-serif;
  --mainColor: #ff2c57;
  --whiteColor: #ffffff;
  --blackColor: #000000;
  --transition: .5s;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--fontFamily);
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0;
  font-weight: 400;
  color: var(--blackColor);
  line-height: 1;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}
ul li {
  list-style: none;
}

:focus {
  outline: 0 !important;
}

p {
  margin-bottom: 0;
  line-height: 1.8;
  color: #727695;
}

a {
  cursor: pointer;
  color: #222222;
  text-decoration: none;
  transition: var(--transition);
}
a:hover, a:focus {
  color: var(--mainColor);
  text-decoration: none;
}

img {
  max-width: 100%;
}

.ptb-100 {
  padding-bottom: 100px;
  padding-top: 100px;
}

.pt-100 {
  padding-top: 100px;
}

.pb-100 {
  padding-bottom: 100px;
}

.ptb-70 {
  padding-top: 70px;
  padding-bottom: 70px;
}

.pt-70 {
  padding-top: 70px;
}

.pb-70 {
  padding-bottom: 70px;
}

.d-table {
  width: 100%;
  height: 100%;
}
.d-table-cell {
  vertical-align: middle;
}

.bg-fffdfd {
  background: #fffdfd;
}

.bg-F7F5F4 {
  background-color: #F7F5F4;
}

.ml-auto {
  margin-left: auto;
}

/*Btn Btn-Primary*/
.btn {
  padding: 10px 30px;
  border-radius: 30px;
  transition: var(--transition);
  font-size: 16px;
  font-weight: 500;
}

.btn-primary {
  color: var(--whiteColor);
  background-color: var(--mainColor);
  border-color: var(--mainColor);
}
.btn-primary:hover, .btn-primary:focus, .btn-primary.disabled:hover, .btn-primary.disabled:focus {
  color: var(--mainColor);
  background-color: var(--whiteColor);
  border-color: var(--mainColor);
}
.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active {
  color: var(--whiteColor);
  background-color: var(--mainColor);
  border-color: var(--mainColor);
}
.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus {
  box-shadow: none;
}
.btn-primary.focus, .btn-primary:focus {
  box-shadow: none;
}
.btn-primary.disabled, .btn-primary:disabled {
  color: var(--whiteColor);
  background-color: var(--mainColor);
  border-color: var(--mainColor);
  opacity: 1;
}

/* Section Title */
.section-title {
  margin-bottom: 60px;
  padding-left: 30px;
  position: relative;
}
.section-title::before, .section-title::after {
  content: "";
  position: absolute;
  width: 3px;
  height: 100%;
  left: 10px;
  top: 0;
  background: var(--mainColor);
  transition: var(--transition);
}
.section-title::after {
  left: 0;
  height: 50%;
}
.section-title:hover::before {
  height: 50%;
}
.section-title:hover::after {
  height: 100%;
}
.section-title h2 {
  margin-bottom: 20px;
  font-size: 40px;
  font-weight: 400;
}
.section-title h2 span {
  color: var(--mainColor);
  font-weight: 600;
}
.section-title p {
  max-width: 635px;
  margin-top: -7px;
}
.section-title h4 {
  color: var(--mainColor);
  margin-bottom: 14px;
  text-transform: capitalize;
  font-size: 17px;
  font-weight: 400;
}

/* Form Control */
.form-control {
  height: 45px;
  padding: 10px;
  font-size: 15px;
  background-color: var(--whiteColor);
  border: 1px solid #eeeeee;
  border-radius: 0;
}
.form-control:focus {
  border-color: #dddddd;
  outline: 0;
  box-shadow: none;
}

textarea.form-control {
  height: auto;
}

/* Owl Carousel Button */
.owl-theme .owl-dots .owl-dot span {
  width: 15px;
  height: 15px;
  margin: 0 4px;
  background: transparent;
  border-radius: 50%;
  border: 2px solid #dddddd;
  transition: var(--transition);
}
.owl-theme .owl-dots .owl-dot.active span {
  background: #869791;
  border-radius: 5px;
  width: 35px;
  height: 10px;
  border-color: var(--mainColor);
  background: var(--mainColor);
}
.owl-theme .owl-dots .owl-dot:hover span {
  background: var(--mainColor);
  border-color: var(--mainColor);
}

/*==============================
Preloader CSS
==============================*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  background: var(--whiteColor);
  z-index: 99999;
}
.preloader .folding-cube {
  left: 0;
  width: 60px;
  height: 60px;
  position: absolute;
  transform: rotateZ(45deg);
  top: 50%;
  right: 0;
  margin: -60px auto 0;
}
.preloader .folding-cube .cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  transform: scale(1.1);
}
.preloader .folding-cube .cube::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--mainColor);
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  transform-origin: 100% 100%;
}
.preloader .folding-cube .cube2 {
  transform: scale(1.1) rotateZ(90deg);
}
.preloader .folding-cube .cube2::before {
  animation-delay: 0.3s;
}
.preloader .folding-cube .cube3 {
  transform: scale(1.1) rotateZ(180deg);
}
.preloader .folding-cube .cube3::before {
  animation-delay: 0.6s;
}
.preloader .folding-cube .cube4 {
  transform: scale(1.1) rotateZ(270deg);
}
.preloader .folding-cube .cube4::before {
  animation-delay: 0.9s;
}

@keyframes sk-foldCubeAngle {
  0%, 10% {
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
/*==============================
Main Header CSS
==============================*/
.navbar {
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  position: fixed !important;
  background: transparent !important;
  padding-top: 30px;
  padding-bottom: 30px;
}
.navbar.sticky-box-shadow {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  position: fixed !important;
  background: var(--whiteColor) !important;
  box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
  animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
  padding-top: 20px;
  padding-bottom: 20px;
}

.navbar-light .navbar-brand {
  color: var(--whiteColor);
  text-transform: uppercase;
  line-height: 10px;
  transition: var(--transition);
  font-size: 23px;
  font-weight: 600;
}
.navbar-light .navbar-brand span {
  color: var(--mainColor);
  line-height: 10px;
  text-transform: lowercase;
  transition: var(--transition);
  font-size: 40px;
  font-weight: 700;
}
.navbar-light .navbar-brand:focus, .navbar-light .navbar-brand:hover {
  color: var(--whiteColor);
}
.navbar-light .navbar-nav .nav-item {
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 0;
  padding-bottom: 0;
}
.navbar-light .navbar-nav .nav-item:last-child {
  padding-right: 0;
}
.navbar-light .navbar-nav .nav-item:first-child {
  padding-left: 0;
}
.navbar-light .navbar-nav .nav-item .nav-link {
  color: var(--whiteColor);
  position: relative;
  cursor: pointer;
  font-weight: 500;
  transition: var(--transition);
  padding-right: 0;
  padding-left: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.navbar-light .navbar-nav .nav-item .nav-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--mainColor);
  transition: var(--transition);
  transform: scaleX(0);
}
.navbar-light .navbar-nav .nav-item .nav-link.active, .navbar-light .navbar-nav .nav-item .nav-link:hover, .navbar-light .navbar-nav .nav-item .nav-link:focus {
  color: var(--mainColor);
}
.navbar-light .navbar-nav .nav-item .nav-link.active::before, .navbar-light .navbar-nav .nav-item .nav-link:hover::before, .navbar-light .navbar-nav .nav-item .nav-link:focus::before {
  transform: scaleX(1);
}
.navbar-light .navbar-nav.for-responsive {
  display: none;
}
.navbar-light.sticky-box-shadow .navbar-brand {
  color: var(--blackColor);
}
.navbar-light.sticky-box-shadow .navbar-nav .nav-link {
  color: var(--blackColor);
}
.navbar-light.sticky-box-shadow .navbar-nav .nav-link.active, .navbar-light.sticky-box-shadow .navbar-nav .nav-link.show, .navbar-light.sticky-box-shadow .navbar-nav .nav-link:hover, .navbar-light.sticky-box-shadow .navbar-nav .nav-link:focus {
  color: var(--mainColor);
}
.navbar-light.sticky-box-shadow .navbar-nav .active .nav-link, .navbar-light.sticky-box-shadow .navbar-nav .show .nav-link {
  color: var(--mainColor);
}

@media only screen and (max-width: 991px) {
  .navbar {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .navbar-light .navbar-nav {
    background-color: var(--whiteColor);
    padding: 10px 20px;
    margin-top: 10px;
    display: none;
  }
  .navbar-light .navbar-nav .nav-item {
    padding-left: 0;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .navbar-light .navbar-nav .nav-item .nav-link {
    color: var(--blackColor);
    display: inline-block;
  }
  .navbar-light .navbar-nav .nav-item .nav-link.active, .navbar-light .navbar-nav .nav-item .nav-link:hover {
    color: var(--mainColor);
  }
  .navbar-light .navbar-nav.for-responsive {
    display: block !important;
  }
  .navbar-light .navbar-toggler {
    color: var(--whiteColor);
    font-size: inherit;
    box-shadow: unset;
    border: none;
    padding: 0;
  }
  .navbar-light .navbar-toggler .burger-menu {
    cursor: pointer;
  }
  .navbar-light .navbar-toggler .burger-menu span {
    height: 2px;
    width: 30px;
    margin: 6px 0;
    display: block;
    background: var(--whiteColor);
  }
  .navbar-light.active .navbar-toggler .burger-menu span.top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }
  .navbar-light.active .navbar-toggler .burger-menu span.middle-bar {
    opacity: 0;
  }
  .navbar-light.active .navbar-toggler .burger-menu span.bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
    margin-top: 5px;
  }
  .navbar-light.active .collapse:not(.show) {
    display: block;
  }
  .navbar-light.sticky-box-shadow .navbar-nav {
    border: 1px solid #eeeeee;
  }
  .navbar-light.sticky-box-shadow .navbar-toggler {
    color: var(--blackColor);
  }
  .navbar-light.sticky-box-shadow .navbar-toggler .burger-menu span {
    background: var(--blackColor);
  }
}
