/*==============================
Creative Banner CSS
==============================*/
.creative-banner-one {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-width: 10rem 100vw 0 0;
  border-color: transparent var(--whiteColor) var(--whiteColor) transparent;
  border-style: solid;
}

.creative-banner-two {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
.creative-banner-two::before {
  content: "";
  border-right: 50vw solid var(--whiteColor);
  border-top: 160px solid transparent;
  bottom: 0;
  right: 0;
  position: absolute;
  height: 0;
  width: 0;
}
.creative-banner-two::after {
  content: "";
  position: absolute;
  border-left: 50vw solid var(--whiteColor);
  border-top: 160px solid transparent;
  bottom: 0;
  height: 0;
  left: 0;
  width: 0;
}

.creative-banner-three {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
}
.creative-banner-three::before {
  content: "";
  position: absolute;
  bottom: -25px;
  left: 0;
